<template>
  <div class="content">
    <van-nav-bar title="邀请好友" left-text="返回" right-text left-arrow @click-left="goBack"/>
    <div class="row-text">徒弟完成首单，我获得{{conf.sun_first}}元。每完成1单，我获得{{conf.sun_first_other}}元</div>
    <div class="row-text">徒孙完成首单，我获得{{conf.sun_second}}元。每完成1单，我获得{{conf.sun_second_other}}元</div>
    <div class="row-text tip-r">假如您邀请了10位徒弟，且每位徒弟各邀请了10位徒孙，若每人每月做20单，您每月可得1000多元分红奖励</div>
    <div class="invite-title">--分享我的邀请链接--</div>
    <van-cell-group>
      <van-field type="textarea" :value="user_url" />
      <van-button
        class="btn"
        :loading="false"
        type="default"
        :bottom-action="true"
        v-clipboard:copy="user_url"
        v-clipboard:success="onCopy"
        v-clipboard:error="onError"
      >一键复制</van-button>
    </van-cell-group>
    <van-cell-group>
      <van-button class="btn" :bottom-action="true" @click="toRefresh " type="primary">刷新链接</van-button>
    </van-cell-group>
    <div class="row-text tip-r">
        <p>每个链接只能使用1次。1小时后失效</p>
<!--        <p>徒弟位有限，暂定20位</p>-->
        <p>只能邀请可靠的好友，若您的好友出现损害平台利益的行为，将没收您的分红奖励，并取消获取分红资格。</p>
    </div>
    <div class="row-text">
        <p>徒弟需要满足以下条件：（若不满足无法审核通过哦）</p>
        <p>1.淘气值满500</p>
        <p>2.买家信誉满3心</p>
        <p>3.周点数<3</p>
        <p>4.tb号注册满两年以上</p>
        <p>5.照妖镜查号无负面记录</p>
        <p>注册过程需要提交身份证正反面哦！</p>
        <p>邀请后，请让您的徒弟（孙）添加您的微信客服微信进行审核哦！</p>
        <p class="tip-r">重要提醒-四不要</p>
        <p class="tip-r">您的徒弟（孙）不要用同一个设备进行任务！</p>
        <p class="tip-r">您的徒弟（孙）不要在同一个IP（wifi）下进行任务！</p>
        <p class="tip-r">您的徒弟（孙）不要和你用相同姓名的银行卡！</p>
        <p class="tip-r">您的徒弟（孙）不要用和您一样或者接近的收货地址！</p>
        <p class="tip-r">如果因以上原因被商家撤单，将扣除对应的佣金</p>
    </div>
  </div>
</template>
<script>
import {
  Row,
  Col,
  CellGroup,
  Cell,
  Button,
  Dialog,
  ImagePreview,
  Toast,
  Field
} from "vant";
import { CONF } from "@/api/user";

export default {
  name: "invites",
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    [Button.name]: Button,
    [Toast.name]: Toast,
    [ImagePreview.name]: ImagePreview
  },
  created() {
    this.initPage()
  },
  data() {
    return {
      conf:[],
      user_url: ''
    };
  },
  methods: {
    initPage(){
      this.$reqGet(CONF)
        .then(res => {
            this.getConf(res)
        }).catch((err) => {
        })
    },
    getConf(res){
      let result = res.data;
      this.conf = result.data
      let host = window.location.host
      let path = window.location.pathname
      let str = this.conf.user_code+'/'+this.conf.auth_time+'/'+this.conf.auth_code
      this.user_url ='http://'+host+path+'#/login/invite/'+str
    },
    onCopy(){
      Toast("复制成功");
    },
    onError() {
      Toast("复制失败");
    },
    toRefresh(){
      location.reload();
    },
    //返回
    goBack() {
      this.$router.push({name: 'index'});
    },
  }
};
</script>
<style lang="scss" scoped>
.row-text{
  background-color: #ffff;
  padding: 15px;
}
.invite-title{
  text-align: center;
  background-color: #ffff;
  padding: 12px 0;
  font-weight: bold;
  margin-top: 5px;
}
.tip-r{
  color:red;
}
.btn{
  margin-bottom: 8px;
}
</style>



